import React, { useEffect } from 'react';
import gsap from 'gsap';
import { TextPlugin } from "gsap/TextPlugin";
import profile from "../Assets/profile.png";
import "../Styles/About.css";

gsap.registerPlugin(TextPlugin);

function About() {

    useEffect(() => {
        gsap.fromTo(".profile-image-container", .5, { scale: 0 }, { opacity: 1, scale: 1, delay: 1 })
        gsap.fromTo(".profile-image", .5, { y: 50 }, { y: 0, opacity: 1, ease: "back.out(4)", delay: 1.5 });

        let tl = gsap.timeline({ delay: 2, repeat: -1, ease: "back.out(1.2)" });

        // set text
        tl.to('.heading', .1, { text: "Hi, I'm mo", y: "-100px" })
            .to('.text', .1, { text: "A motivated problem solver who enjoys coding and building solution", y: "-100px" })

            //////////////////////////////////////////////////////////// First Cycle
            // drop down
            .to('.heading', .5, { y: 0, opacity: 1 })
            .to('.text', .5, { y: 0, opacity: 1 }, "-=.2")

            // hold
            .to('.text', 7, { x: 0 })

            // go down
            .to('.heading', .5, { y: "50px", opacity: 0 })
            .to('.text', .5, { y: "65px", opacity: 0 }, "-=.2")

            // change Text
            .to('.heading', .1, { text: "Front-End Developer" })
            .to('.text', .1, { text: "React, Angular, Vue, React Native, GSAP, and more" })

            // go up
            .to('.heading', .1, { y: "-100" })
            .to('.text', .1, { y: "-100" })



            //////////////////////////////////////////////////////////// Second Cycle
            // drop down
            .to('.heading', .5, { y: 0, opacity: 1 })
            .to('.text', .5, { y: 0, opacity: 1 }, "-=.2")

            // hold
            .to('.text', 7, { x: 0 })

            // go down
            .to('.heading', .5, { y: "50px", opacity: 0 })
            .to('.text', .5, { y: "65px", opacity: 0 }, "-=.2")

            // change Text
            .to('.heading', .1, { text: "Backend Developer" })
            .to('.text', .1, { text: "JavaScript, Python, PHP, Node.js, Express.js, Django, Laravel and more" })

            // go up
            .to('.heading', .1, { y: "-100" })
            .to('.text', .1, { y: "-100" })

            //////////

            // drop down
            .to('.heading', .5, { y: 0, opacity: 1 })
            .to('.text', .5, { y: 0, opacity: 1 }, "-=.2")

            // hold
            .to('.text', 7, { x: 0 })

            // go down
            .to('.heading', .5, { y: "50px", opacity: 0 })
            .to('.text', .5, { y: "65px", opacity: 0 }, "-=.2")
    }, [])

    return <section id="about" className="about flex">
        <div className="container flex flex-column center p15" style={{ paddingTop: "70px" }}>

            <div className="flex center profile-image-container">
                <img src={profile} className="profile-image" alt="profile" />
            </div>
            <div className="flex flex-column center">
                <div className="overflow-hidden intro-heading">
                    <h1 className="heading">Hi, I'm mo</h1>
                </div>
                <div className="overflow-hidden intro-text">
                    <p className="text">A motivated problem solver who enjoys coding and building solutions</p>
                </div>
            </div>

        </div>
    </section>;
}
export default About;