import React, { useEffect, useState, useRef } from 'react';
import gsap from "gsap";
import "../Styles/Projects.css";

const projects = [
    {
        title: 'Bug Tracker',
        tags: ['Node.js', 'Express.js', 'React', 'MongoDB'],
        details: 'Bug Tracker app, built using MERN Stack.',
        github: 'https://github.com/moElhaj/mern-bug-tracker',
        demo: 'https://mo-bug-tracker.herokuapp.com',
        image: `${require("../Assets/bug.JPG")}`
    },
    {
        title: 'Expenses Tracker',
        tags: ['Node.js', 'Express.js', 'React', 'MongoDB'],
        details: 'Track your finances to know where your money is going.',
        github: 'https://github.com/moElhaj/expenses-tracker',
        demo: 'https://mo-expenses.herokuapp.com',
        image: `${require("../Assets/expenses.jpg")}`
    },
    {
        title: 'Guess the word',
        tags: ['Angular'],
        details: 'Guess the word using the given clues. A fun game built using Angular.',
        github: 'https://github.com/moElhaj/angular-word-game',
        demo: 'https://mo-game.herokuapp.com',
        image: `${require("../Assets/game.JPG")}`
    },
    {
        title: 'ML cars prices',
        tags: ['Flask', 'Python'],
        details: 'Estimate cars prices based on features specified by you using Machine Learning.',
        github: 'https://github.com/moElhaj/Machine-Learning-Car-Estimator',
        demo: 'https://codex-garage.herokuapp.com',
        image: `${require("../Assets/car.JPG")}`
    },
    {
        title: 'Football App',
        tags: ['React Native'],
        details: 'Follow premier league, live scores, and standings',
        github: 'https://github.com/moElhaj/react-native-football-app',
        demo: 'https://expo.io/@moelhaj/kora',
        image: `${require("../Assets/kora.jpg")}`
    },
    {
        title: 'Employee Management',
        tags: ['PHP'],
        details: 'Payrolls and Human Resources Management.',
        github: 'https://github.com/moElhaj/php-employees-management',
        image: `${require("../Assets/hrms.jpg")}`
    },
    {
        title: 'Task Management',
        tags: ['ASP.NET'],
        details: 'Manage tasks, sort by priority and flow due dates.',
        github: 'https://github.com/moElhaj/aspnet-task-management',
        image: `${require("../Assets/task.jpg")}`
    },
    {
        title: 'ETL Pipeline',
        tags: ['SQL', 'Python'],
        details: 'Extracting data from csv, transforming it, and loading into a Data Warehouse.',
        github: 'https://github.com/moElhaj/elt-pipeline',
        image: `${require("../Assets/etl.jpg")}`
    }
];

function Projects() {

    const projectDiv = useRef();
    const [current, setCurrent] = useState(0);

    useEffect(() => {

    }, [])

    const next = () => {
        let projectWidth = projectDiv.current.getBoundingClientRect().width;
        gsap.to(['.project-title','.project-id'], 1, { y: '100px' });
        gsap.to('.project-curtain', 1, { width: "100%" })
        setTimeout(() => {
            current >= projects.length - 1 ? setCurrent(0) : setCurrent(current + 1);
        }, 900)
        gsap.to(['.project-title','.project-id'], 1, { y: 0, delay: 1 });
        gsap.to('.project-curtain', 1, { x: projectWidth, delay: 1 })
        gsap.to('.project-curtain', 0, { width: "0%", x: 0, delay: 2 })
    }

    const previous = () => {
        let projectWidth = projectDiv.current.getBoundingClientRect().width;
        gsap.to(['.project-title','.project-id'], 1, { y: '100px' });
        gsap.to('.project-curtain', 1, { width: "100%" })
        setTimeout(() => {
            current === 0 ? setCurrent(projects.length - 1) : setCurrent(current - 1);
        }, 900)
        gsap.to(['.project-title','.project-id'], 1, { y: 0, delay: 1 });
        gsap.to('.project-curtain', 1, { x: projectWidth, delay: 1 })
        gsap.to('.project-curtain', 0, { width: "0%", x: 0, delay: 2 })
    }


    return <section id="projects" className="projects flex center">
        <div className="container p15">

            <div ref={projectDiv} className="project" style={{ backgroundImage: `url(${projects[current].image})` }}>

                <div className="project-curtain"></div>

                <div className="project-content w-100 h-100 flex flex-column">

                    <div className="flex">
                        <div className="box">
                            <h3 className="project-title">{projects[current].title}</h3>
                        </div>
                        <div className="spacer"></div>
                        <div className="box">
                            <h3 className="project-id">{current < 10 ? `0${current + 1}` : current + 1}</h3>
                        </div>
                    </div>


                    <div className="box" style={{ marginTop: "30px" }}>
                        <p className="project-details">{projects[current].details}</p>
                    </div>

                    <div className="box" style={{ marginTop: "30px" }}>
                        <div className="flex project-tags">
                            {projects[current].tags.map((tag, index) => (
                                <div key={index} className="tag flex v-center">{tag}</div>
                            ))}
                        </div>
                    </div>

                    <div className="box" style={{ marginTop: "30px" }}>
                        <div className="flex v-center project-links">

                            {projects[current].demo && <a className="flex v-center project-link" href={projects[current].demo} target="_blank" rel="noopener noreferrer">
                                <svg xmlns="http://www.w3.org/2000/svg" width="25" height="25" fill="currentColor" className="bi bi-box-arrow-up-right" viewBox="0 0 16 16">
                                    <path fillRule="evenodd" d="M8.636 3.5a.5.5 0 0 0-.5-.5H1.5A1.5 1.5 0 0 0 0 4.5v10A1.5 1.5 0 0 0 1.5 16h10a1.5 1.5 0 0 0 1.5-1.5V7.864a.5.5 0 0 0-1 0V14.5a.5.5 0 0 1-.5.5h-10a.5.5 0 0 1-.5-.5v-10a.5.5 0 0 1 .5-.5h6.636a.5.5 0 0 0 .5-.5z" />
                                    <path fillRule="evenodd" d="M16 .5a.5.5 0 0 0-.5-.5h-5a.5.5 0 0 0 0 1h3.793L6.146 9.146a.5.5 0 1 0 .708.708L15 1.707V5.5a.5.5 0 0 0 1 0v-5z" />
                                </svg>
                            </a>}

                            {projects[current].github && <a className="flex v-center project-link" href={projects[current].github} target="_blank" rel="noopener noreferrer">
                                <svg xmlns="http://www.w3.org/2000/svg" width="25" height="25" fill="currentColor" className="bi bi-github" viewBox="0 0 16 16">
                                    <path d="M8 0C3.58 0 0 3.58 0 8c0 3.54 2.29 6.53 5.47 7.59.4.07.55-.17.55-.38 0-.19-.01-.82-.01-1.49-2.01.37-2.53-.49-2.69-.94-.09-.23-.48-.94-.82-1.13-.28-.15-.68-.52-.01-.53.63-.01 1.08.58 1.23.82.72 1.21 1.87.87 2.33.66.07-.52.28-.87.51-1.07-1.78-.2-3.64-.89-3.64-3.95 0-.87.31-1.59.82-2.15-.08-.2-.36-1.02.08-2.12 0 0 .67-.21 2.2.82.64-.18 1.32-.27 2-.27.68 0 1.36.09 2 .27 1.53-1.04 2.2-.82 2.2-.82.44 1.1.16 1.92.08 2.12.51.56.82 1.27.82 2.15 0 3.07-1.87 3.75-3.65 3.95.29.25.54.73.54 1.48 0 1.07-.01 1.93-.01 2.2 0 .21.15.46.55.38A8.012 8.012 0 0 0 16 8c0-4.42-3.58-8-8-8z" />
                                </svg>
                            </a>}

                        </div>
                    </div>

                </div>




            </div>

            <div className="project-controls flex h-center">
                <svg onClick={previous} xmlns="http://www.w3.org/2000/svg" width="25" height="25" fill="currentColor" className="bi bi-arrow-left" viewBox="0 0 16 16">
                    <path fillRule="evenodd" d="M15 8a.5.5 0 0 0-.5-.5H2.707l3.147-3.146a.5.5 0 1 0-.708-.708l-4 4a.5.5 0 0 0 0 .708l4 4a.5.5 0 0 0 .708-.708L2.707 8.5H14.5A.5.5 0 0 0 15 8z" />
                </svg>
                <svg onClick={next} xmlns="http://www.w3.org/2000/svg" width="25" height="25" fill="currentColor" className="bi bi-arrow-right" viewBox="0 0 16 16">
                    <path fillRule="evenodd" d="M1 8a.5.5 0 0 1 .5-.5h11.793l-3.147-3.146a.5.5 0 0 1 .708-.708l4 4a.5.5 0 0 1 0 .708l-4 4a.5.5 0 0 1-.708-.708L13.293 8.5H1.5A.5.5 0 0 1 1 8z" />
                </svg>
            </div>
        </div>
    </section>
}

export default Projects;