import React, { useState, useEffect } from "react";
import "../Styles/Header.css";
import gsap from "gsap";
import { ScrollToPlugin } from 'gsap/ScrollToPlugin';

gsap.registerPlugin(ScrollToPlugin);

function hamburgerOpen() {
    // toggler
    gsap.to('.line2', {
        duration: .2,
        opacity: 0,
    })
    gsap.to('.line1', {
        duration: .4,
        y: "8.9px"
    })
    gsap.to('.line1', {
        delay: .4,
        rotation: 45
    })
    gsap.to('.line3', {
        duration: .4,
        y: "-8.9px"
    })
    gsap.to('.line3', {
        delay: .4,
        rotation: -45
    })
}

function hamburgerClose() {
    // toggler
    gsap.to('.line1', {
        rotation: 0,
        duration: .4,
    })
    gsap.to('.line1', {
        delay: .4,
        y: 0
    })
    gsap.to('.line3', {
        rotation: 0,
        duration: .4,
    })
    gsap.to('.line3', {
        delay: .4,
        y: 0
    })
    gsap.to('.line2', {
        delay: .5,
        duration: .1,
        opacity: 1,
        display: "block"
    })
}

function open() {
    let tl = gsap.timeline({ ease: "power4.inOut" });
    tl.to('.menu', 0, { display: "flex" })
        .to('.menu-curtain', .5, { width: "100%" })
        .to('.menu-content', .5, { width: "100%" }, "-=.3")
        .to('.link', .3, { x: 0, stagger: .1 })
        .to('.contact-link', .3, { opacity: 1, stagger: .1 });
    hamburgerOpen()
}


function close() {
    let tl = gsap.timeline({ ease: "power4.inOut" });
    tl.to('.contact-link', .3, { opacity: 0, stagger: .1 })
        .to('.link', .3, { x: "-1000px", stagger: .1 }, "-=.3")
        .to('.menu-content', .5, { width: "0%" })
        .to('.menu-curtain', .5, { width: "0%" }, "-=.3")
        .to('.menu', 0, { display: "none" })
    hamburgerClose()

}

function Header() {

    const [currentPage, setCurrentPage] = useState('about');
    const [toggled, setToggled] = useState(false);
    const [disable, setDisable] = useState(false);

    useEffect(() => {
        gsap.to('#header', .7, { y: 0, delay: .5 });
    }, [])

    function toggle() {
        setDisable(true)
        if (toggled === true) {
            close();
            setToggled(false);
        } else {
            open();
            setToggled(true);
        }
        setTimeout(() => {
            setDisable(false);
        }, 1500)
    }

    function navigate(target) {
        let pageWidth = window.innerWidth;
        // document.querySelectorAll('section').forEach(section => {
        //     section.style.display = "none";
        // })
        let tl = gsap.timeline();
        tl.to(`#${target}`, 0, { x: pageWidth, display: "flex" })
        tl.to(`#${currentPage}`, .7, { x: -pageWidth })
        tl.to(`#${currentPage}`, 0, { display: "none" })
        tl.to(`#${target}`, .7, { x: 0 })

        setCurrentPage(target);
    }

    function hoverAnimation() {
        let tl = gsap.timeline()
        tl.to('.dot', .3, { x: 10, ease: "back.out(1.7)" })
            .to('.dot', .3, { x: 0 })
    }

    return <>
        <header className="flex" id="header">
            <div className="logo" onClick={hoverAnimation}>mo<span className="dot">.</span></div>
            <div className="spacer"></div>
            <div className="toggler flex">
                <button onClick={toggle} disabled={disable}>
                    <span className="toggler-line line1"></span>
                    <span className="toggler-line line2"></span>
                    <span className="toggler-line line3"></span>
                </button>
            </div>
        </header>
        <div className="menu">
            <div className="menu-curtain"></div>
            <div className="menu-content">
                <div className="links flex flex-column center">
                    <div className="link" onClick={() => { toggle(); navigate('about') }}>
                        <div className="link-title">about</div>
                        <div className="link-info">Information about me.</div>
                    </div>
                    <div className="link" onClick={() => { toggle(); navigate('projects') }}>
                        <div className="link-title">projects</div>
                        <div className="link-info">A list of my projects.</div>
                    </div>
                    <div className="link" onClick={() => { toggle(); navigate('contact') }}>
                        <div className="link-title">contact</div>
                        <div className="link-info">Get in touch.</div>
                    </div>
                </div>
                <div className="contact-links flex">
                    <a className="contact-link" href="https://www.linkedin.com/in/mo-elhaj/" target="_blank" rel="noopener noreferrer">
                        <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24"><path d="M4.98 3.5c0 1.381-1.11 2.5-2.48 2.5s-2.48-1.119-2.48-2.5c0-1.38 1.11-2.5 2.48-2.5s2.48 1.12 2.48 2.5zm.02 4.5h-5v16h5v-16zm7.982 0h-4.968v16h4.969v-8.399c0-4.67 6.029-5.052 6.029 0v8.399h4.988v-10.131c0-7.88-8.922-7.593-11.018-3.714v-2.155z" /></svg>
                    </a>
                    <a className="contact-link" href="https://github.com/moelhaj" target="_blank" rel="noopener noreferrer">
                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M12 2c5.514 0 10 4.486 10 10s-4.486 10-10 10-10-4.486-10-10 4.486-10 10-10zm0-2c-6.627 0-12 5.373-12 12s5.373 12 12 12 12-5.373 12-12-5.373-12-12-12zm0 6c-3.313 0-6 2.686-6 6 0 2.651 1.719 4.9 4.104 5.693.3.056.396-.13.396-.289v-1.117c-1.669.363-2.017-.707-2.017-.707-.272-.693-.666-.878-.666-.878-.544-.373.041-.365.041-.365.603.042.92.619.92.619.535.917 1.403.652 1.746.499.054-.388.209-.652.381-.802-1.333-.152-2.733-.667-2.733-2.965 0-.655.234-1.19.618-1.61-.062-.153-.268-.764.058-1.59 0 0 .504-.161 1.65.615.479-.133.992-.199 1.502-.202.51.002 1.023.069 1.503.202 1.146-.776 1.648-.615 1.648-.615.327.826.121 1.437.06 1.588.385.42.617.955.617 1.61 0 2.305-1.404 2.812-2.74 2.96.216.186.412.551.412 1.111v1.646c0 .16.096.347.4.288 2.383-.793 4.1-3.041 4.1-5.691 0-3.314-2.687-6-6-6z" /></svg>
                    </a>
                </div>
            </div>
        </div>
    </>;
}

export default Header;